<script>
export default {
  name: "App",
  data() {
    return {
      select_main: true,
      select_challenge: true,
      select_dlc1: true,
      select_dlc2: true,
      missionpackVisiblity: true,
      logVisiblity: true,
      target: [],
      log: [],
      result: this.$missions.Main[0],
      result_challenge: 0,
    };
  },
  mounted() {
    this.targetselect();
    this.start();
  },
  components: {},
  methods: {
    targetselect() {
      let list = [];
      if (this.select_main) {
        list = list.concat(this.$missions.Main);
        if (this.select_online) {
          list = list.concat(this.$missions.Online);
        }
      }
      if (this.select_dlc1) {
        list = list.concat(this.$missions.DLC1);
      }
      if (this.select_dlc2) {
        list = list.concat(this.$missions.DLC2);
      }
      if (!this.select_main && !this.select_dlc1 && !this.select_dlc2) {
        list = null;
        this.log.unshift("ランダム化するミッションパックを選択してください");
      }
      this.target = list;
      console.log("ターゲット変更");
      console.log(this.target);
    },
    start() {
      let elm = document.getElementsByClassName("transition");
      for (var i = 0; i < elm.length; i++) {
        elm[i].classList.add("nowloading");
      }
      console.log(elm);
      setTimeout(this.shuffle, 800);
    },
    challenge_reroll() {
      this.result_challenge = Math.floor(Math.random() * 20);
    },
    shuffle() {
      if (this.target == null) {
        this.result = {
          onID: "--",
          ID: "ミッションパックを選択してください",
          name: "",
          status: "0",
        };
      } else {
        const rnd = Math.floor(Math.random() * this.target.length);
        this.result = this.target[rnd];
        this.result_challenge = Math.floor(Math.random() * 20);
        this.log.unshift(this.target[rnd].name + "が選択されました");
      }
      let elm = document.getElementsByClassName("transition");
      for (var i = 0; i < elm.length; i++) {
        elm[i].classList.remove("nowloading");
      }
    },
  },
};
</script>

